<template>
<div class="mobile-page partner-news-container">
    <MainLayout :title="$t('Common.PartnerNew.Title')" :leftIcon="`LEFT_ARROW`" :leftAction="() => this.$router.go(-1)">
        <div class="padding-content">
            <InputSearch :search="fetchSearchPartnerNews" />
            <div class="items-CY">
                <template v-for="(partnerNew, index) in partnerNews">
                    <PartnerNewItem :partnerNew="partnerNew" :key="index" />
                </template>
                <InfiniteLoading :isSearch="isSearch" :isError="isError" :isComplete="isComplete" :fetchData="fetchPartnerNews" :classParent="'.items-CY'" />
            </div>
        </div>

    </MainLayout>
</div>
</template>

<script>
import MainLayout from '@/components/Common/Layout/MainLayout'
import PartnerNewItem from '@/components/PartnerNew/PartnerNewItem'
import InputSearch from '@/components/Common/Input/InputSearch'
import InfiniteLoading from '@/components/Common/Loading/InfiniteLoading'
import {
    CODE_BBS
} from '@/constants'
import {
    SHOW_POPUP_BUTTON
} from '@/mutations/Common.mutation'
import {
    FETCH_PARTNER_NEWS,
    CREATE_PARTNER_NEWS
} from '@/mutations/PartnerNew.mutation'

export default {
    name: 'PartnerNews',
    components: {
        MainLayout,
        PartnerNewItem,
        InputSearch,
        InfiniteLoading
    },
    data: function () {
        return {
            page: 0,
            isComplete: false,
            isError: false,
            isSearch: false,
            searchValue: ''
        }
    },
    computed: {
        partnerNews() {
            return this.$store.state.PartnerNew.partnerNews;
        },
        totalPartnerNews() {
            return this.$store.state.PartnerNew.totalPartnerNews;
        }
    },
    async created() {
        await this.$store.dispatch("CREATE_PARTNER_NEWS");
    },
    methods: {
        async fetchSearchPartnerNews(searchValue = '') {
            await this.resetData();
            this.searchValue = searchValue;
            this.isSearch = true;
        },
        async fetchPartnerNews() {
            try {
                this.page += 1;
                let params = {
                    bbsCd: CODE_BBS.PARTNER_NEW,
                    pageNum: this.page + '',
                    searchValue: this.searchValue,
                }

                await this.$store.dispatch("FETCH_PARTNER_NEWS", params);
                if (this.totalPartnerNews == 0 || this.partnerNews.length == this.totalPartnerNews) {
                    this.isComplete = true;
                }
                this.isSearch = false;
            } catch (error) {
                this.$store.dispatch(SHOW_POPUP_BUTTON, error.message);
                this.isError = true;
            }
        },
        resetData() {
            this.$store.dispatch('CREATE_PARTNER_NEWS')
            this.page = 0;
            this.isComplete = false;
            this.isError = false;
        }
    },
    beforeDestroy() {
        this.searchValue = '';
        this.resetData();
    }
}
</script>

<style lang="scss">
.partner-news-container {
    .input-search {
        background-color: #F5F5F5;
        position: absolute;
        width: 100%;
    }
}
</style>
