<template>
<div class="item" v-if="partnerNew" @click="viewPartnerNewDetail">
    <div class="item-title">{{partnerNew.artclSbj ? partnerNew.artclSbj : ''}}</div>
    <div class="item-header d-flex justify-content-between">
        <div class="time">{{partnerNewTime}}</div>
    </div>
</div>
</template>

<script>
import moment from 'moment'

export default {
    name: 'PartnerNewItem',
    components: {},
    data: function () {
        return {
            partnerNewTime: null,
        }
    },
    props: {
        partnerNew: null
    },
    async created() {
        if (this.partnerNew.regDt) {
            this.partnerNewTime = moment(this.partnerNew.time).format("YYYY.MM.DD HH:mm");
        }
    },
    methods: {
        viewPartnerNewDetail() {
            if (this.partnerNew.artclSeq) {
                this.$router.push(`/partner-new/${this.partnerNew.artclSeq}`)
            }
        }
    }
}
</script>
